<table>
<thead>
<tr>
<th>配置项</th>
<th>简介</th>
<th>类型</th>
<th>备注</th>
</tr>
</thead>
<tbody>
<tr>
<td>dimension</td>
<td>维度</td>
<td>string</td>
<td>默认 columns[0]</td>
</tr>
<tr>
<td>metrics</td>
<td>指标</td>
<td>string</td>
<td>默认 columns[1]</td>
</tr>
<tr>
<td>dataType</td>
<td>数据类型</td>
<td>object</td>
<td>-</td>
</tr>
<tr>
<td>digit</td>
<td>设置数据类型为percent时保留的位数</td>
<td>number</td>
<td>默认为2</td>
</tr>
<tr>
<td>labelMap</td>
<td>设置指标的别名</td>
<td>object</td>
<td>-</td>
</tr>
<tr>
<td>seriesMap</td>
<td>附加到 series 中的设置</td>
<td>object</td>
<td>-</td>
</tr>
<tr>
<td>dataName</td>
<td>表盘上显示的单位</td>
<td>object</td>
<td>-</td>
</tr>
</tbody>
</table>
<blockquote>
