<table>
<thead>
<tr>
<th>配置项</th>
<th>简介</th>
<th>类型</th>
<th>备注</th>
</tr>
</thead>
<tbody>
<tr>
<td>dimension</td>
<td>维度</td>
<td>string</td>
<td>默认columns第一项为维度</td>
</tr>
<tr>
<td>metrics</td>
<td>指标</td>
<td>string</td>
<td>默认columns第二项为指标</td>
</tr>
<tr>
<td>dataType</td>
<td>数据类型</td>
<td>string</td>
<td>可选值: KMB, normal, percent</td>
</tr>
<tr>
<td>sequence</td>
<td>数据显示顺序</td>
<td>array</td>
<td>默认按照数据大小顺序</td>
</tr>
<tr>
<td>ascending</td>
<td>是否显示为金字塔</td>
<td>boolean</td>
<td>默认为false</td>
</tr>
<tr>
<td>useDefaultOrder</td>
<td>是否自动按照数值大小排序</td>
<td>boolean</td>
<td>默认为false</td>
</tr>
<tr>
<td>filterZero</td>
<td>是否过滤指标为0的数据</td>
<td>boolean</td>
<td>默认为false</td>
</tr>
<tr>
<td>digit</td>
<td>设置数据类型为percent时保留的位数</td>
<td>number</td>
<td>默认为2</td>
</tr>
<tr>
<td>label</td>
<td>设置文本标签样式</td>
<td>object</td>
<td>内容参考<a href="http://echarts.baidu.com/option.html#series-funnel.label">文档</a></td>
</tr>
<tr>
<td>labelLine</td>
<td>设置标签的视觉引导线样式</td>
<td>object</td>
<td>内容参考<a href="http://echarts.baidu.com/option.html#series-funnel.labelLine">文档</a></td>
</tr>
<tr>
<td>itemStyle</td>
<td>设置图形样式</td>
<td>object</td>
<td>内容参考<a href="http://echarts.baidu.com/option.html#series-funnel.itemStyle">文档</a></td>
</tr>
</tbody>
</table>

