<table>
<thead>
<tr>
<th>配置项</th>
<th>简介</th>
<th>类型</th>
<th>备注</th>
</tr>
</thead>
<tbody>
<tr>
<td>dimension</td>
<td>维度</td>
<td>String</td>
<td>默认 <code>columns[0]</code></td>
</tr>
<tr>
<td>metrics</td>
<td>指标</td>
<td>String</td>
<td>默认 <code>columns[1]</code></td>
</tr>
<tr>
<td>sizeMin</td>
<td>最小字体大小</td>
<td>Number</td>
<td>默认为 <code>12</code></td>
</tr>
<tr>
<td>sizeMax</td>
<td>最大字体大小</td>
<td>Number</td>
<td>默认为 <code>60</code></td>
</tr>
<tr>
<td>shape</td>
<td>词云图的形状</td>
<td>String</td>
<td>默认为 <code>circle</code>，可选值见备注</td>
</tr>
<tr>
<td>color</td>
<td>词云图字体颜色</td>
<td>Array[String], String, Function</td>
<td>默认为 <code>&quot;rgb(Math.round(Math.random * 160), Math.round(Math.random * 160), Math.round(Math.random * 160))&quot;</code></td>
</tr>
</tbody>
</table>
<blockquote>
<p>备注1: shape 可选值有cardioid、 diamond、triangle-forward、triangle、pentagon、star等。如果最终的形状不是理想的形状（都为矩形），可能是画布高度或者宽度不够以及数据过多导致的。可通过设置画布或容器的width或height，通过extend设置series的drawOutOfBound尝试解决。</p>
</blockquote>
<blockquote>
<p>备注2: 详细配置详见<a href="https://github.com/ecomfe/echarts-wordcloud">echarts-wordcloud文档</a>。更多个性化配置可以结合wordcloud文档在extend的series配置项中设置。</p>
</blockquote>

