<table>
<thead>
<tr>
<th>配置项</th>
<th>简介</th>
<th>类型</th>
<th>备注</th>
</tr>
</thead>
<tbody>
<tr>
<td>dimension</td>
<td>维度</td>
<td>string</td>
<td>默认为 columns[0]</td>
</tr>
<tr>
<td>metrics</td>
<td>指标</td>
<td>array</td>
<td>默认为 [columns[1] ~ columns[5]]</td>
</tr>
<tr>
<td>digit</td>
<td>设置数据类型为percent时保留的位数</td>
<td>number</td>
<td>默认为2</td>
</tr>
<tr>
<td>itemStyle</td>
<td>图形样式</td>
<td>object</td>
<td>内容参考<a href="http://echarts.baidu.com/option.html#series-candlestick.itemStyle">文档</a></td>
</tr>
<tr>
<td>labelMap</td>
<td>设置指标的别名</td>
<td>object</td>
<td>-</td>
</tr>
<tr>
<td>legendName</td>
<td>设置图表上方图例的别名</td>
<td>object</td>
<td>-</td>
</tr>
<tr>
<td>showMA</td>
<td>是否展示移动平均线指标</td>
<td>boolean</td>
<td>默认 为 false</td>
</tr>
<tr>
<td>MA</td>
<td>移动平均线指标周期</td>
<td>array</td>
<td>默认 <code>[5, 10, 20, 30]</code></td>
</tr>
<tr>
<td>showVol</td>
<td>是否展示成交量</td>
<td>boolean</td>
<td>默认为 false</td>
</tr>
<tr>
<td>downColor</td>
<td>下降颜色</td>
<td>string</td>
<td>默认为 #ec0000</td>
</tr>
<tr>
<td>upColor</td>
<td>上升颜色</td>
<td>string</td>
<td>默认为 #00da3c</td>
</tr>
<tr>
<td>showDataZoom</td>
<td>是否展示 dataZoom 控件</td>
<td>boolean</td>
<td>默认为 false</td>
</tr>
<tr>
<td>start</td>
<td>dataZoom 控件的起始位置</td>
<td>number</td>
<td>默认为 50，即中间位置</td>
</tr>
<tr>
<td>end</td>
<td>dataZoom 控件的结束位置</td>
<td>number</td>
<td>默认为 100，即末尾位置</td>
</tr>
<tr>
<td>dataType</td>
<td>数据展示格式</td>
<td>string</td>
<td>可选值: KMB, normal, percent</td>
</tr>
</tbody>
</table>
<blockquote>
<p>备注1: metrics 各项的值分别代表 <code>['open', 'close', 'lowest', 'highest', 'vol']</code>（开盘值, 收盘值, 最低值, 最高值, 成交量）, 其中 vol 为可选值。
