</code></pre></template></demo-block><h4 id="she-zhi-zi-ding-yi-wei-zhi-bing-xiu-gai-shu-ju-yuan"><a class="header-anchor" href="#she-zhi-zi-ding-yi-wei-zhi-bing-xiu-gai-shu-ju-yuan">¶</a> 设置自定义位置并修改数据源</h4>
<demo-block>
        
        <template slot="source"><by-demo6 /></template>
        <template slot="highlight"><pre v-pre><code class="html">&lt;template&gt;
  &lt;ve-map :data=&quot;chartData&quot; :settings=&quot;chartSettings&quot; :extend=&quot;chartExtend&quot;&gt;&lt;/ve-map&gt;
&lt;/template&gt;

&lt;script&gt;
  export default {
    data () {
      this.chartSettings = {
        positionJsonLink: 'https://unpkg.com/v-charts-custom-maps@0.2.1/hk-geo.json',
        position: 'HK',
        beforeRegisterMap (json) {
          // edit data here such as:
          // json.features[0].properties.cp = [121.509062, 26.044332]
          return json
        }
      }
      this.chartExtend = {
        series: {
          nameMap: {
            'Central and Western':'中西区',
            'Eastern':'东区',
            'Islands':'离岛',
            'Kowloon City':'九龙城',
            'Kwai Tsing':'葵青',
            'Kwun Tong':'观塘',
            'North':'北区',
            'Sai Kung':'西贡',
            'Sha Tin':'沙田',
            'Sham Shui Po':'深水埗',
            'Southern':'南区',
            'Tai Po':'大埔',
            'Tsuen Wan':'荃湾',
            'Tuen Mun':'屯门',
            'Wan Chai':'湾仔',
            'Wong Tai Sin':'黄大仙',
            'Yau Tsim Mong':'油尖旺',
            'Yuen Long':'元朗'
          }
        }
      }
      return {
        chartData: {
          columns: ['位置', ' 人口'],
          rows: [
            { '位置': '北区', ' 人口': 123 }
          ]
        }
      }
    }
  }
&lt;/script&gt;
</code></pre></template></demo-block><h4 id="settings-pei-zhi-xiang"><a class="header-anchor" href="#settings-pei-zhi-xiang">¶</a> settings 配置项</h4>
<table>
<thead>
<tr>
<th>配置项</th>
<th>简介</th>
<th>类型</th>
<th>备注</th>
</tr>
</thead>
<tbody>
<tr>
<td>dimension</td>
<td>维度</td>
<td>string</td>
<td>默认columns第一项为维度</td>
</tr>
<tr>
<td>metrics</td>
<td>指标</td>
<td>array</td>
<td>默认columns第二项为指标</td>
</tr>
<tr>
<td>position</td>
<td>位置</td>
<td>string</td>
<td>默认为 <code>'china'</code></td>
</tr>
<tr>
<td>selectData</td>
<td>是否高亮显示数据对应位置</td>
<td>boolean</td>
<td>默认为 <code>false</code></td>
</tr>
<tr>
<td>selectedMode</td>
<td>地图选中模式</td>
<td>Boolean，String</td>
<td>默认为 <code>false</code>, 可选值有<br><code>'single', 'multiple'</code></td>
</tr>
<tr>
<td>dataType</td>
<td>指标数据类型</td>
<td>object</td>
<td>内容为 指标-指标数据 <br>类型的键值对，支持<br><code>'normal', 'KMB', 'percent'</code></td>
</tr>
<tr>
<td>digit</td>
<td>设置数据类型为percent时保留的位数</td>
<td>number</td>
<td>默认为2</td>
</tr>
<tr>
<td>zoom</td>
<td>视角的缩放比例</td>
<td>number</td>
<td>默认为1</td>
</tr>
<tr>
<td>center</td>
<td>当前视角的中心点</td>
<td>array</td>
<td>-</td>
</tr>
<tr>
<td>aspectScale</td>
<td>用于 scale 地图的长宽比</td>
<td>number</td>
<td>默认为0.75</td>
</tr>
<tr>
<td>boundingCoords</td>
<td>左上角以及右下角分别所对应的经纬度</td>
<td>array</td>
<td>默认为null</td>
</tr>
<tr>
<td>roam</td>
<td>是否开启鼠标缩放和平移漫游</td>
<td>boolean, string</td>
<td>默认为false, 可选值有<br><code>false, true, 'scale', 'move'</code></td>
</tr>
<tr>
<td>scaleLimit</td>
<td>滚轮缩放的极限控制</td>
<td>object</td>
<td>默认<code>{ min: 1, max: 1 }</code></td>
</tr>
<tr>
<td>mapGrid</td>
<td>地图距离容器的边距</td>
<td>object</td>
<td>默认值为<br><code>{</code><br><code>left: auto,</code><br><code>right: auto,</code><br><code>top: auto,</code><br><code>bottom: auto</code><br><code>}</code></td>
</tr>
<tr>
<td>label</td>
<td>文本标签</td>
<td>boolean, object</td>
<td>默认为<code>true</code>, 内容参考<a href="http://echarts.baidu.com/option.html#series-map.label">文档</a></td>
</tr>
<tr>
<td>itemStyle</td>
<td>地图区域的多边形 图形样式</td>
<td>boolean, object</td>
<td>默认为true, 内容参考<a href="http://echarts.baidu.com/option.html#series-map.itemStyle">文档</a></td>
</tr>
<tr>
<td>positionJsonLink</td>
<td>地图数据源</td>
<td>string</td>
<td>-</td>
</tr>
<tr>
<td>beforeRegisterMap</td>
<td>地图数据注册前执行的函数</td>
<td>Function</td>
<td>参数为地图数据，需返回地图数据</td>
</tr>
<tr>
<td>beforeRegisterMapOnce</td>
<td>地图数据注册前执行的函数(仅执行一次)</td>
<td>Function</td>
<td>参数为地图数据，需返回地图数据</td>
</tr>
<tr>
<td>specialAreas</td>
<td>地图中的位置配置</td>
<td>object</td>
<td>将地图中的部分区域缩放到合适的位置，可以使得整个地图的显示更加好看, 用法参考<a href="http://echarts.baidu.com/api.html#echarts.registerMap">文档</a></td>
</tr>
<tr>
<td>mapURLProfix</td>
<td>位置请求的 URL 前缀</td>
<td>string</td>
<td>默认为 <code>https://unpkg.com/echarts@3.6.2/map/json/</code></td>
</tr>
<tr>
<td>mapOrigin</td>
<td>地图 geojson</td>
<td>object</td>
<td>-</td>
</tr>
</tbody>
</table>
<blockquote>
<p>备注: 属性中的 position 默认为'china',可设置的类型有'china'、'china-cities'、'china-contour'、'world'、'province/beijing'、'province/shanghai'等，省份的position如例子中所示需要在前面加'province/'</p>
</blockquote>

