<table>
<thead>
<tr>
<th>配置项</th>
<th>简介</th>
<th>类型</th>
<th>备注</th>
</tr>
</thead>
<tbody>
<tr>
<td>dimension</td>
<td>维度</td>
<td>string</td>
<td>默认columns第一项为维度</td>
</tr>
<tr>
<td>metrics</td>
<td>指标</td>
<td>string</td>
<td>默认columns第二项为指标</td>
</tr>
<tr>
<td>dataType</td>
<td>数据类型</td>
<td>string</td>
<td>可选值: KMB, normal, percent</td>
</tr>
<tr>
<td>totalNum</td>
<td>总量</td>
<td>number</td>
<td>默认瀑布图总量为所有数据的和</td>
</tr>
<tr>
<td>totalName</td>
<td>总量的显示文案</td>
<td>string</td>
<td>默认显示总计</td>
</tr>
<tr>
<td>remainName</td>
<td>剩余的显示文案</td>
<td>string</td>
<td>默认显示其他</td>
</tr>
<tr>
<td>digit</td>
<td>设置数据类型为percent时保留的位数</td>
<td>number</td>
<td>默认为2</td>
</tr>
</tbody>
</table>

