&lt;/script&gt;
</code></pre></template></demo-block><h4 id="settings-pei-zhi-xiang"><a class="header-anchor" href="#settings-pei-zhi-xiang">¶</a> settings 配置项</h4>
<table>
<thead>
<tr>
<th>配置项</th>
<th>简介</th>
<th>类型</th>
<th>备注</th>
</tr>
</thead>
<tbody>
<tr>
<td>dimension</td>
<td>维度</td>
<td>String</td>
<td>默认 columns[0]</td>
</tr>
<tr>
<td>metrics</td>
<td>指标</td>
<td>String</td>
<td>默认 columns[1]</td>
</tr>
<tr>
<td>dataType</td>
<td>数据类型</td>
<td>String</td>
<td>默认为percent,也可设置为normal</td>
</tr>
<tr>
<td>digit</td>
<td>设置数据类型为percent时保留的位数</td>
<td>Number</td>
<td>默认为2</td>
</tr>
<tr>
<td>wave</td>
<td>设置水球图分层</td>
<td>Array[Number]，Array[Array[Number]]</td>
<td>默认为[], 数组中的值不大于row中对应的维度值，且需要由大到小排列。可设置为[0.3, 0.2, 0.1]的形式，表示每个水球图都显示4层，且最下面三层的值分别为0.1，0.2，0.3；也可设置为[[0.2, 0.1], [0.1], []]的形式，当有多个水球图时，wave中的每一项对应一个水球图的分层设置，如果wave.length &lt; rows.length,则剩余的水球图的分层设置以wave数组中最后一项为准。比如rows=[{val: 0.1}, {val: 0.2}, {val: 0.3}, {val: 0.4}], 而wave=[[0.2,0.1], [0.2]]，此时rows中后三项的分层设置均为[0.2]</td>
</tr>
<tr>
<td>seriesMap</td>
<td>附加到 series 中的设置</td>
<td>Object，Array[Object]</td>
<td>默认为{}，可以设置单个水球图，也可以一次设置多个。数组形式配置见“多个水球图”示例，对象形式配置见其他示例。</td>
</tr>
</tbody>
</table>
<blockquote>
<p>备注1： 通过 seriesMap，可以为每一个水球图设置样式，具体样式的配置可以参考<a href="https://github.com/ecomfe/echarts-liquidfill">文档</a>或下面对应的中文文档。</p>
</blockquote>
<h3 id="echarts-liquidfill-pei-zhi-zhong-wen-wen-dang-zaiseriesmap-zhong-shi-yong"><a class="header-anchor" href="#echarts-liquidfill-pei-zhi-zhong-wen-wen-dang-zaiseriesmap-zhong-shi-yong">¶</a> echarts-liquidfill配置中文文档(在seriesMap中使用))</h3>
<h4 id="seriesmap-pei-zhi-xiang"><a class="header-anchor" href="#seriesmap-pei-zhi-xiang">¶</a> seriesMap 配置项</h4>
<table>
<thead>
<tr>
<th>配置项</th>
<th>简介</th>
<th>类型</th>
<th>备注</th>
</tr>
</thead>
<tbody>
<tr>
<td>color</td>
<td>颜色</td>
<td>Array[String]</td>
<td>data中第一个数据对应color中第一个颜色，以此类推。如果color.length小于data.length, data中数据项的颜色会循环展示。在这里，color设置的是row中metrics和wave的值对应数据波浪的颜色。</td>
</tr>
<tr>
<td>radius</td>
<td>水球图的半径</td>
<td>String</td>
<td>单个水球图的半径，相对于容器元素中较短边(width和height)的长度，以百分数表示,如’50%’</td>
</tr>
<tr>
<td>center</td>
<td>水球图的位置</td>
<td>Array[String]</td>
<td>设置时以百分数表示，数组中第一项代表横坐标，相对于容器的width的百分比，第二项代表纵坐标，相对于容器heigght的百分比。如[’50%’, ’50%’]表示居中</td>
</tr>
<tr>
<td>amplitude</td>
<td>水波的起伏程度</td>
<td>String</td>
<td>用百分数表示。数值越大，起伏程度越大，如’10%’</td>
</tr>
<tr>
<td>waveLength</td>
<td>水波的长度</td>
<td>String</td>
<td>相对于水球的直径。当为’50%’时，水球图中一条数据上会出现两个水波；为33%时，会出现三个水波。以此类推。</td>
</tr>
<tr>
<td>period</td>
<td>水波滚动的速度</td>
<td>Number, Function</td>
<td>表示滚动一个水波需要花费的时间，以毫秒为单位。可以直接设置数字，也可以设置为function(value, index)的形式。</td>
</tr>
<tr>
<td>direction</td>
<td>水波的滚动方向</td>
<td>String</td>
<td>整体设置水波的滚动方向，可选’left’或’right’</td>
</tr>
<tr>
<td>shape</td>
<td>水球图的形状</td>
<td>String</td>
<td>水球图的形状，可选值有’circle’, ‘rect’, ‘roundRect’, ‘triangle’, ‘diamond’, ‘pin’, ‘arrow’，也可以通过svg自定义’path://*'</td>
</tr>
<tr>
<td>waveAnimation</td>
<td>水波是否滚动</td>
<td>Boolean</td>
<td>为true时水波滚动，反之不滚动。默认为true</td>
</tr>
<tr>
<td>animationEasing</td>
<td>水波初始的动画的速度曲线</td>
<td>String</td>
<td>水波初始的动画的速度曲线</td>
</tr>
<tr>
<td>animationEasingUpdate</td>
<td>数据变动时水波动画的速度曲线</td>
<td>String</td>
<td>数据变动时水波动画的速度曲线</td>
</tr>
<tr>
<td>animationDuration</td>
<td>水波初始动画所需要的时间</td>
<td>Number</td>
<td>水波初始动画所需要的时间</td>
</tr>
<tr>
<td>animationDurationUpdate</td>
<td>数据变动时水波动画变化所需要的时间</td>
<td>Number</td>
<td>数据变动时水波动画变化所需要的时间</td>
</tr>
<tr>
<td>outline</td>
<td>水球图的外边框设置</td>
<td>Object</td>
<td>水球图的外边框设置</td>
</tr>
<tr>
<td>backgroundStyle</td>
<td>水球图的背景颜色</td>
<td>Object</td>
<td>水球图的背景颜色</td>
</tr>
<tr>
<td>itemStyle</td>
<td>水球图波浪的样式</td>
<td>Object</td>
<td>水球图波浪的样式</td>
</tr>
<tr>
<td>emphasis</td>
<td>鼠标悬浮时波浪的样式</td>
<td>Object</td>
<td>鼠标悬浮时波浪的样式</td>
</tr>
<tr>
<td>label</td>
<td>图形上的文本标签</td>
<td>Object</td>
<td>图形上的文本标签</td>
</tr>
</tbody>
</table>
<p>!&gt; 注意：不要在seriesMap中设置data，这样会覆盖row中对应的数据和wave。</p>
<h5 id="outline-shui-qiu-tu-wai-bian-kuang-pei-zhi"><a class="header-anchor" href="#outline-shui-qiu-tu-wai-bian-kuang-pei-zhi">¶</a> &quot;outline&quot; 水球图外边框配置</h5>
<table>
<thead>
<tr>
<th>配置项</th>
<th>简介</th>
<th>类型</th>
<th>备注</th>
</tr>
</thead>
<tbody>
<tr>
<td>show</td>
<td>是否显示</td>
<td>Boolean</td>
<td></td>
</tr>
<tr>
<td>borderDistance</td>
<td></td>
<td>Number</td>
<td>水球图外边框和水球图核心内容的距离，类似于元素的内边距</td>
</tr>
<tr>
<td>itemStyle</td>
<td>水球图外边框的样式</td>
<td>Object</td>
<td>主要配置项有color: String(边框和核心内容之间的背景颜色，也就是borderDistance那部分的背景颜色), borderColor: String(外边框的颜色)，borderWidth: Number(外边框的宽度)，shadowBlur: Number(外边阴影的模糊距离)，shadowColor: String(边框阴影的颜色)</td>
</tr>
</tbody>
</table>
