<table>
<thead>
<tr>
<th>配置项</th>
<th>简介</th>
<th>类型</th>
<th>备注</th>
</tr>
</thead>
<tbody>
<tr>
<td>dimension</td>
<td>维度</td>
<td>string</td>
<td>默认columns第一项为维度</td>
</tr>
<tr>
<td>metrics</td>
<td>指标</td>
<td>array</td>
<td>默认columns第二项起为指标</td>
</tr>
<tr>
<td>dataType</td>
<td>数据类型</td>
<td>object</td>
<td>可选值: KMB, normal, percent</td>
</tr>
<tr>
<td>digit</td>
<td>设置数据类型为percent时保留的位数</td>
<td>number</td>
<td>默认为2</td>
</tr>
<tr>
<td>label</td>
<td>图形上的文本标签</td>
<td>object</td>
<td>内容参考<a href="http://echarts.baidu.com/option.html#series-radar.label">文档</a></td>
</tr>
<tr>
<td>itemStyle</td>
<td>折线拐点标志的样式</td>
<td>object</td>
<td>内容参考<a href="http://echarts.baidu.com/option.html#series-radar.itemStyle">文档</a></td>
</tr>
<tr>
<td>lineStyle</td>
<td>线条样式</td>
<td>object</td>
<td>内容参考<a href="http://echarts.baidu.com/option.html#series-radar.lineStyle">文档</a></td>
</tr>
<tr>
<td>areaStyle</td>
<td>区域填充样式</td>
<td>object</td>
<td>内容参考<a href="http://echarts.baidu.com/option.html#series-radar.areaStyle">文档</a></td>
</tr>
</tbody>
</table>
<blockquote>
<p>备注：dataType中直接设置对应维度的数据类型，例如示例的<code>{ '占比': 'percent' }</code>，即将占比数据设置为百分比类型</p>
</blockquote>

