<table>
<thead>
<tr>
<th>配置项</th>
<th>简介</th>
<th>类型</th>
<th>备注</th>
</tr>
</thead>
<tbody>
<tr>
<td>type</td>
<td>热力图的类型</td>
<td>string</td>
<td>可选值：<code>cartesian</code>(默认值，直角坐标系), <code>map</code>(地图)，<code>bmap</code>(百度地图)</td>
</tr>
<tr>
<td>xAxisList</td>
<td>x 轴数据</td>
<td>array</td>
<td>默认取数据中的数据中的第一维度的数据</td>
</tr>
<tr>
<td>yAxisList</td>
<td>y 轴数据</td>
<td>array</td>
<td>默认取数据中的数据中的第二维度的数据</td>
</tr>
<tr>
<td>dimension</td>
<td>维度</td>
<td>array</td>
<td>默认为 [columns[0], columns[1]]</td>
</tr>
<tr>
<td>metrics</td>
<td>指标</td>
<td>string</td>
<td>默认为 columns[2]</td>
</tr>
<tr>
<td>dataType</td>
<td>数据类型</td>
<td>string</td>
<td>可选值: KMB, normal, percent</td>
</tr>
<tr>
<td>min</td>
<td>visualMap 中的最小值</td>
<td>number</td>
<td>默认取指标中最小的数据</td>
</tr>
<tr>
<td>max</td>
<td>visualMap 中的最大值</td>
<td>number</td>
<td>默认取指标中最大的数据</td>
</tr>
<tr>
<td>digit</td>
<td>设置数据类型为percent时保留的位数</td>
<td>number</td>
<td>默认为2</td>
</tr>
<tr>
<td>key</td>
<td>百度地图 access_key</td>
<td>string</td>
<td>可<a href="http://lbsyun.baidu.com/apiconsole/key">由此</a>获取</td>
</tr>
<tr>
<td>bmap</td>
<td>百度地图配置项</td>
<td>object</td>
<td>参考<a href="https://github.com/ecomfe/echarts/tree/master/extension/bmap#%E4%BD%BF%E7%94%A8">文档</a>配置</td>
</tr>
<tr>
<td>geo</td>
<td>地图配置项</td>
<td>object</td>
<td>参考<a href="http://echarts.baidu.com/option.html#geo">文档</a></td>
</tr>
<tr>
<td>position</td>
<td>地图类型</td>
<td>string</td>
<td>默认为 <code>'china'</code></td>
</tr>
<tr>
<td>positionJsonLink</td>
<td>地图数据源</td>
<td>string</td>
<td>-</td>
</tr>
<tr>
<td>beforeRegisterMap</td>
<td>地图数据注册前执行的函数</td>
<td>Function</td>
<td>参数为地图数据，需返回地图数据</td>
</tr>
<tr>
<td>beforeRegisterMapOnce</td>
<td>地图数据注册前执行的函数(仅执行一次)</td>
<td>Function</td>
<td>参数为地图数据，需返回地图数据</td>
</tr>
<tr>
<td>specialAreas</td>
<td>地图中的位置配置</td>
<td>object</td>
<td>将地图中的部分区域缩放到合适的位置，可以使得整个地图的显示更加好看, 用法参考<a href="http://echarts.baidu.com/api.html#echarts.registerMap">文档</a></td>
</tr>
<tr>
<td>mapURLProfix</td>
<td>位置请求的 URL 前缀</td>
<td>string</td>
<td>默认为 <code>https://unpkg.com/echarts@3.6.2/map/json/</code></td>
</tr>
<tr>
<td>pointSize</td>
<td>点大小</td>
<td>number</td>
<td>默认为 10</td>
</tr>
<tr>
<td>blurSize</td>
<td>模糊大小</td>
<td>number</td>
<td>默认为 5</td>
</tr>
<tr>
<td>heatColor</td>
<td>visualMap 中的最大值颜色区间</td>
<td>array</td>
<td>-</td>
</tr>
<tr>
<td>yAxisName</td>
<td>y 轴名称</td>
<td>string</td>
<td>-</td>
</tr>
<tr>
<td>xAxisName</td>
<td>x 轴名称</td>
<td>string</td>
<td>-</td>
</tr>
</tbody>
</table>
<blockquote>
<p>备注：当不指定指标时，指标的值默认为1。</p>
</blockquote>

