        }
      }
    }
  }
&lt;/script&gt;
</code></pre></template></demo-block><h4 id="settings-pei-zhi-xiang"><a class="header-anchor" href="#settings-pei-zhi-xiang">¶</a> settings 配置项</h4>
<table>
<thead>
<tr>
<th>配置项</th>
<th>简介</th>
<th>类型</th>
<th>备注</th>
</tr>
</thead>
<tbody>
<tr>
<td>dimension</td>
<td>维度</td>
<td>string</td>
<td>默认columns第一项为维度</td>
</tr>
<tr>
<td>metrics</td>
<td>指标</td>
<td>string</td>
<td>默认columns第二项为指标</td>
</tr>
<tr>
<td>dataType</td>
<td>数据类型</td>
<td>string</td>
<td>可选值: KMB, normal, percent</td>
</tr>
<tr>
<td>legendLimit</td>
<td>legend显示数量限制</td>
<td>number</td>
<td>legend数量过多会导致饼图样式错误，限制legend最大值并且当超过此值时，隐藏legend可以解决这个问题</td>
</tr>
<tr>
<td>selectedMode</td>
<td>选中模式</td>
<td>string</td>
<td>可选值：single, multiple，默认为false</td>
</tr>
<tr>
<td>hoverAnimation</td>
<td>是否开启 hover 在扇区上的放大动画效果</td>
<td>boolean</td>
<td>默认值为true</td>
</tr>
<tr>
<td>radius</td>
<td>饼图半径</td>
<td>number</td>
<td>-</td>
</tr>
<tr>
<td>offsetY</td>
<td>纵向偏移量</td>
<td>number</td>
<td>-</td>
</tr>
<tr>
<td>digit</td>
<td>设置数据类型为percent时保留的位数</td>
<td>number</td>
<td>默认为2</td>
</tr>
<tr>
<td>roseType</td>
<td>显示为南丁格尔玫瑰图</td>
<td>string</td>
<td>默认不展示为南丁格尔玫瑰图，可设置为<code>'radius', 'area'</code></td>
</tr>
<tr>
<td>label</td>
<td>饼图图形上的文本标签</td>
<td>object</td>
<td>内容参考<a href="http://echarts.baidu.com/option.html#series-pie.label">文档</a></td>
</tr>
<tr>
<td>labelLine</td>
<td>标签的视觉引导线样式</td>
<td>object</td>
<td>内容参考<a href="http://echarts.baidu.com/option.html#series-pie.labelLine">文档</a></td>
</tr>
<tr>
<td>itemStyle</td>
<td>图形样式</td>
<td>object</td>
<td>内容参考<a href="http://echarts.baidu.com/option.html#series-pie.itemStyle">文档</a></td>
</tr>
<tr>
<td>level</td>
<td>多圆饼图时设置</td>
<td>array</td>
<td>-</td>
</tr>
<tr>
<td>limitShowNum</td>
<td>设置超过此数字时使用‘其他’代替</td>
<td>number</td>
<td>此时数据会按照由大到小顺序显示</td>
</tr>
</tbody>
</table>
<blockquote>
<p>备注1. level 的值接受二维数组，例如：<code>[['a', 'b'], ['c', 'd']]</code>, 表示的含义是内层展示的是维度中的<code>'a', 'b'</code>的指标加在一起组成的饼图，外层为<code>'c', 'd'</code>的指标加在一起组成的环图。</p>
</blockquote>

