</code></pre></template></demo-block><h4 id="settings-pei-zhi-xiang"><a class="header-anchor" href="#settings-pei-zhi-xiang">¶</a> settings 配置项</h4>
<table>
<thead>
<tr>
<th>配置项</th>
<th>简介</th>
<th>类型</th>
<th>备注</th>
</tr>
</thead>
<tbody>
<tr>
<td>dimension</td>
<td>维度</td>
<td>array</td>
<td>默认columns第一项为维度</td>
</tr>
<tr>
<td>metrics</td>
<td>指标</td>
<td>array</td>
<td>默认columns第二项起为指标</td>
</tr>
<tr>
<td>yAxisType</td>
<td>左右坐标轴数据类型</td>
<td>array</td>
<td>可选值: KMB, normal, percent</td>
</tr>
<tr>
<td>yAxisName</td>
<td>左右坐标轴标题</td>
<td>array</td>
<td>-</td>
</tr>
<tr>
<td>axisSite</td>
<td>指标所在的轴</td>
<td>object</td>
<td>默认不在right轴的指标都在left轴</td>
</tr>
<tr>
<td>stack</td>
<td>堆叠选项</td>
<td>object</td>
<td>-</td>
</tr>
<tr>
<td>digit</td>
<td>设置数据类型为percent时保留的位数</td>
<td>number</td>
<td>默认为2</td>
</tr>
<tr>
<td>dataOrder</td>
<td>设置数据排序方式</td>
<td>boolean, object</td>
<td>默认为false</td>
</tr>
<tr>
<td>scale</td>
<td>是否是脱离 0 值比例</td>
<td>array</td>
<td>默认为[false, false]，表示左右<br>两个轴都不会脱离0值比例。<br>设置成 true 后坐标刻度不会<br>强制包含零刻度<br></td>
</tr>
<tr>
<td>min</td>
<td>左右坐标轴最小值</td>
<td>array</td>
<td>-</td>
</tr>
<tr>
<td>max</td>
<td>左右坐标轴最大值</td>
<td>array</td>
<td>-</td>
</tr>
<tr>
<td>labelMap</td>
<td>设置指标的别名，同时作用于提示框和图例</td>
<td>object</td>
<td>-</td>
</tr>
<tr>
<td>legendName</td>
<td>设置图表上方图例的别名</td>
<td>object</td>
<td>-</td>
</tr>
<tr>
<td>label</td>
<td>设置图形上的文本标签</td>
<td>object</td>
<td>内容参考<a href="http://echarts.baidu.com/option.html#series-bar.label">文档</a></td>
</tr>
<tr>
<td>itemStyle</td>
<td>图形样式</td>
<td>object</td>
<td>内容参考<a href="http://echarts.baidu.com/option.html#series-bar.itemStyle">文档</a></td>
</tr>
<tr>
<td>showLine</td>
<td>展示为折线图的指标</td>
<td>array</td>
<td>-</td>
</tr>
<tr>
<td>xAxisType</td>
<td>横轴的类型</td>
<td>string</td>
<td>可选值'category'，'value'，默认为'category'</td>
</tr>
<tr>
<td>opacity</td>
<td>透明度</td>
<td>number</td>
<td>-</td>
</tr>
</tbody>
</table>
<blockquote>
<p>备注1. axisSite 可以设置 left 和 right，例如示例所示 <code>axisSite: { right: ['占比'] }</code> 即将占比的数据置于右轴上。</p>
</blockquote>
<blockquote>
<p>备注2. stack 用于将两数据堆叠起来，例如实例中所示<code>stack: { '销售额': ['销售额-1季度', '销售额-2季度'] }</code> 即将'销售额-1季度', '销售额-2季度'相应的数据堆叠在一起。</p>
</blockquote>
<blockquote>
<p>备注3. dataOrder 用于设置数据的排序方式，用于更加清晰的展示数据的升降。例如： <code>{ label: '余额', order: 'asc }</code> 表示数据按照余额指标升序展示，降序为<code>desc</code>。</p>
</blockquote>
<blockquote>
<p>备注4. min和max的值可以直接设置为数字，例如：<code>[100, 300]</code>；也可以设置为<code>['dataMin', 'dataMin']</code>, <code>['dataMax', 'dataMax']</code>，此时表示使用该坐标轴上的最小值或最大值为最小或最大刻度。</p>
</blockquote>
<blockquote>
<p>备注5. 有时我们需要将折线图与柱状图展示在同一个图上，利用showLine属性可以设置需要展示为折线图的指标，其他的指标则使用柱状图展示。</p>
</blockquote>
<blockquote>
<p>备注6. 为了优化连续的数值型横轴显示多指标的时候样式，在此情况下默认设置opacity为0.5。</p>
</blockquote>

