
    <template>
      <section class="content">
        <h1>百度/高德地图</h1>
<p>为了使在echarts上更简单的使用百度/高德地图，v-charts封装了一个百度/高德地图的‘壳子’，在settings中添加关于图表的配置（key,bmap,amap），在组件上直接设置 series, tooltip 等，便可生成以百度/高德地图为坐标系的 Echarts 图表。</p>
<h4 id="bai-du-di-tu"><a class="header-anchor" href="#bai-du-di-tu">¶</a> 百度地图</h4>
<demo-block>
        <div><p>需要将 <code>key</code> 替换为公司自己的地图key</p>
</div>
        <template slot="source"><by-demo0 /></template>
        <template slot="highlight"><pre v-pre><code class="html">&lt;template&gt;
  &lt;ve-bmap
    :settings=&quot;chartSettings&quot;
    :series=&quot;chartSeries&quot;
    :tooltip=&quot;chartTooltip&quot;&gt;
  &lt;/ve-bmap&gt;
&lt;/template&gt;

&lt;script&gt;
  export default {
    data () {
      this.chartSettings = {
        key: 'oBvDtR6nzWtVchkY4cLHtnah1VVZQKRK',
        bmap: {
          center: [116.43, 39.92],
          zoom: 14,
          roam: true,
          mapStyle: {}
        }
      }
      this.chartTooltip = { show: true }
      return {
        chartSeries: [
          {
            type: 'scatter',
            coordinateSystem: 'bmap',
            data: [
              [120, 30, 1] // 经度，维度，value，...
            ]
          }
        ]
      }
    }
  }
&lt;/script&gt;
</code></pre></template></demo-block><h4 id="gao-de-di-tu"><a class="header-anchor" href="#gao-de-di-tu">¶</a> 高德地图</h4>
<demo-block>
        <div><p>需要将 <code>key</code> 替换为公司自己的地图key</p>
</div>
        <template slot="source"><by-demo1 /></template>
        <template slot="highlight"><pre v-pre><code class="html">&lt;template&gt;
  &lt;ve-amap
    :settings=&quot;chartSettings&quot;
    :series=&quot;chartSeries&quot;
    :tooltip=&quot;chartTooltip&quot;&gt;
  &lt;/ve-amap&gt;
&lt;/template&gt;

&lt;script&gt;
  export default {
    data () {
      this.chartSettings = {
        key: '4b5f2cf2cba25200cc6b68c398468899',
        v: '1.4.3',
        amap: {
          resizeEnable: true,
          center: [116.43, 39.92],
          zoom: 10
        }
      }
      this.chartTooltip = { show: true }
      return {
        chartSeries: [
          {
            type: 'scatter',
            coordinateSystem: 'bmap',
            data: [
              [120, 30, 1] // 经度，维度，value，...
            ]
          }
        ]
      }
    }
  }
&lt;/script&gt;
</code></pre></template></demo-block><h4 id="huo-qu-di-tu-shi-li"><a class="header-anchor" href="#huo-qu-di-tu-shi-li">¶</a> 获取地图实例</h4>
<demo-block>
        <div><p>需要将 <code>key</code> 替换为公司自己的地图key</p>
</div>
        <template slot="source"><by-demo2 /></template>
        <template slot="highlight"><pre v-pre><code class="html">&lt;template&gt;
  &lt;ve-bmap
    :settings=&quot;chartSettings&quot;
    :after-set-option-once=&quot;afterSet&quot;
    :series=&quot;chartSeries&quot;
    :tooltip=&quot;chartTooltip&quot;&gt;
  &lt;/ve-bmap&gt;
&lt;/template&gt;

&lt;script&gt;
  export default {
    data () {
      this.chartSettings = {
        key: 'oBvDtR6nzWtVchkY4cLHtnah1VVZQKRK',
        bmap: {
          center: [116.43, 39.92],
          zoom: 14,
          roam: true,
          mapStyle: {}
        }
      }
      this.chartTooltip = { show: true }
      return {
        chartSeries: [
          {
            type: 'scatter',
            coordinateSystem: 'bmap',
            data: [
              [120, 30, 1] // 经度，维度，value，...
            ]
          }
        ]
      }
    },
    methods: {
      afterSet: function (echarts) {
        var bmap = echarts.getModel().getComponent('bmap').getBMap()
        bmap.addControl(new window.BMap.MapTypeControl())
      }
    }
  }
&lt;/script&gt;
</code></pre></template></demo-block><h4 id="settings-pei-zhi-xiang"><a class="header-anchor" href="#settings-pei-zhi-xiang">¶</a> settings 配置项</h4>
<table>
<thead>
<tr>
<th>配置项</th>
<th>简介</th>
<th>类型</th>
<th>备注</th>
</tr>
</thead>
<tbody>
<tr>
<td>key</td>
<td>百度/高德地图 access_key</td>
<td>string</td>
<td>可<a href="http://lbsyun.baidu.com/apiconsole/key">由此</a>获取</td>
</tr>
<tr>
<td>v</td>
<td>百度/高德地图版本</td>
<td>string</td>
<td>默认2.0(百度)/1.4.3(高德)</td>
</tr>
<tr>
<td>bmap</td>
<td>百度地图配置项</td>
<td>object</td>
<td>参考<a href="https://github.com/ecomfe/echarts/tree/master/extension/bmap#%E4%BD%BF%E7%94%A8">文档</a>配置</td>
</tr>
<tr>
<td>amap</td>
<td>高德地图配置项</td>
<td>object</td>
<td>参考高德地图文档配置</td>
</tr>
<tr>
<td>useOuterMap</td>
<td>使用全局的地图依赖</td>
<td>boolean</td>
<td>-</td>
</tr>
</tbody>
</table>
<blockquote>
<ol>
<li>百度/高德地图在与其他地图搭配使用时，需要额外引入相应的 echarts 模块，例如使用热力图，则需要<code>import 'echarts/lib/chart/heatmap'</code>。</li>
<li>使用 script 引入百度地图或高德地图后，配置 useOuterMap 可以阻止组件对地图资源的引用<br>
百度地图 <code>&lt;script src=&quot;https://api.map.baidu.com/api?v=2.0&amp;ak=key&quot;&gt;&lt;/script&gt;</code><br>
高德地图 <code>&lt;script src=&quot;https://webapi.amap.com/maps?v=1.4.3&amp;key=key&quot;&gt;&lt;/script&gt;</code></li>
</ol>
</blockquote>

      </section>
    </template>
